@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto+Mono:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*----------------------------------------------------------------*/
/*Fonts*/
/*Fonts -> Roboto Mono & Montserrat*/
/*----------------------------------------------------------------*/
/*General*/
/*General -> Text*/
* {
  font-family: "Montserrat", sans-serif;
  color: #202020;
}
/*General -> Titles*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto Mono", monospace;
}
/*General -> Profile Picture*/
.profile-picture {
  max-width: inherit;
  border-radius: 50%;
}
/*General -> Body*/
body {
  background-color: white;
  overflow: hidden;
}
/*----------------------------------------------------------------*/
/*Bootstrap Fixes*/
.container,
.container-fluid {
  padding-left: 0;
  padding-right: 0;
}
/*----------------------------------------------------------------*/
/*Z-Index Sheet
0 - 3D Scenes
2 - Navigation Menu
3 - Navigation Buttons
4 - Toasts
*/
/*----------------------------------------------------------------*/
/*Animations*/
/*Contact Me Animation*/
@-webkit-keyframes contactMeAnim {
  0% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
  }
  50% {
    -webkit-transform: scale(1.0625);
            transform: scale(1.0625);
  }
  100% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
  }
}
@keyframes contactMeAnim {
  0% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
  }
  50% {
    -webkit-transform: scale(1.0625);
            transform: scale(1.0625);
  }
  100% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
  }
}
/*About Me Animation*/
@-webkit-keyframes aboutMeAnim {
  0% {
    -webkit-transform: rotate(-22.5deg);
            transform: rotate(-22.5deg);
  }
  50% {
    -webkit-transform: rotate(-20.5deg);
            transform: rotate(-20.5deg);
  }
  100% {
    -webkit-transform: rotate(-22.5deg);
            transform: rotate(-22.5deg);
  }
}
@keyframes aboutMeAnim {
  0% {
    -webkit-transform: rotate(-22.5deg);
            transform: rotate(-22.5deg);
  }
  50% {
    -webkit-transform: rotate(-20.5deg);
            transform: rotate(-20.5deg);
  }
  100% {
    -webkit-transform: rotate(-22.5deg);
            transform: rotate(-22.5deg);
  }
}
/*Skill List Animation*/
@-webkit-keyframes skillListAnim {
  0% {
    -webkit-transform: rotate(-11.25deg);
            transform: rotate(-11.25deg);
  }
  50% {
    -webkit-transform: rotate(-9.25deg);
            transform: rotate(-9.25deg);
  }
  100% {
    -webkit-transform: rotate(-11.25deg);
            transform: rotate(-11.25deg);
  }
}
@keyframes skillListAnim {
  0% {
    -webkit-transform: rotate(-11.25deg);
            transform: rotate(-11.25deg);
  }
  50% {
    -webkit-transform: rotate(-9.25deg);
            transform: rotate(-9.25deg);
  }
  100% {
    -webkit-transform: rotate(-11.25deg);
            transform: rotate(-11.25deg);
  }
}
@-webkit-keyframes skillListWishAnim {
  0% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
}
@keyframes skillListWishAnim {
  0% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
  50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
  }
}
/*Toast Animation*/
@-webkit-keyframes toastAnim {
  0% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
  }
  50% {
    -webkit-transform: scale(1.0625);
            transform: scale(1.0625);
  }
  100% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
  }
}
@keyframes toastAnim {
  0% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
  }
  50% {
    -webkit-transform: scale(1.0625);
            transform: scale(1.0625);
  }
  100% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
  }
}
/*Navigation Button Animation*/
@-webkit-keyframes navigationButtonAnim {
  0% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
  50% {
    -webkit-transform: scale(1.0625);
            transform: scale(1.0625);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
}
@keyframes navigationButtonAnim {
  0% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
  50% {
    -webkit-transform: scale(1.0625);
            transform: scale(1.0625);
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: scale(1.03125);
            transform: scale(1.03125);
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
}
/*Animation Attribution*/
.contacts h3,
.main-aboutme > *,
.main-skills .skill-list > *,
.main-skills .wish {
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}
.contacts h3 {
  -webkit-animation-name: contactMeAnim;
          animation-name: contactMeAnim;
}
.main-aboutme > * {
  -webkit-animation-name: aboutMeAnim;
          animation-name: aboutMeAnim;
}
.main-skills .skill-list > * {
  -webkit-animation-name: skillListAnim;
          animation-name: skillListAnim;
}
.main-skills .wish {
  -webkit-animation-name: skillListWishAnim;
          animation-name: skillListWishAnim;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}
/*----------------------------------------------------------------*/
/*Navigation Menu*/
.navigation-menu {
