}
/*----------------------------------------------------------------*/
/*Contacts*/
.contacts {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding-right: 1rem;
}
.contacts h3 {
  color: #66cdaa;
  margin: 0;
  padding: 0 1em 0 0;
}
/*Contacts -> Contact List*/
.contactList {
