/*----------------------------------------------------------------*/
/*Navigation Buttons*/
.navigationButtons {
  position: absolute;
  z-index: 3;
  bottom: 1rem;
  right: 1rem;
  pointer-events: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.navigationButtons button {
  pointer-events: auto;
  background-color: #66cdaa;
  border: none;
  border-radius: 50%;
  transition: 0.5s ease-in-out;
  padding: 1.5rem 1rem;
